import React from 'react'

const PersonForm = ({submitInfo, newName ,handleNameChange, newNumber,handleNumberChange}) => {
  return (
    <form onSubmit = {submitInfo}>
      <div>
        name: <input value = {newName} onInput = {handleNameChange}/>
        <br/>
        number: <input value = {newNumber} onInput = {handleNumberChange}/>
      </div>
      <div>
        <button type="submit">add</button>
      </div>
    </form>
  )

}

export default PersonForm
