import React, { useState, useEffect } from 'react'
import axios from 'axios'
import Name from './components/Name'
import Filter from './components/Filter'
import PersonForm from './components/PersonForm'
import Notification from './components/Notification'
import entryService from './services/entry'

const App = () => {
  const [persons, setPersons] = useState([]);
  const [ searchList, setSearchList ] = useState([]);
  const [ searchTerm, setSearchTerm ] = useState('');
  const [ newName, setNewName ] = useState('');
  const [ newNumber, setNewNumber ] = useState('');
  const [message, setMessage] = useState(null);
  const [messageStyle, setMessageStyle] = useState('');

  useEffect(() => {
    entryService
     .getAll()
     .then(initialPersons => {
       setPersons(initialPersons)
   })

}, [])


  const searchTermList = (term, list) => {
    return list.filter(item => (item.name).includes(term));
  }

  const checkNameExists = (name) => {
    let nameList = persons.map(person => person.name);
    return nameList.includes(name)
  }

  const addInfo = (name, number) => {
    const infoObject = {
      name: name,
      number: number
    }


    entryService
      .create(infoObject)
        .then(response => {
          setPersons(persons.concat(infoObject));
          setMessageStyle('success')
          setMessage(
            `${infoObject.name} has been added to the phonebook`
          )
          setTimeout(() => {
          setMessage(null)
        }, 5000)
          setNewName('');
          setNewNumber('');
    })
  }

  const updatePersonInfo = (newName, newNumber) => {
    const personInfo = persons.find(person => person.name === newName )
    console.log(personInfo )
    if  (window.confirm(`${personInfo.name}?
      is already added to the phonebook,replace the old number with a new one?`))
       {
        const updatedPerson = {...personInfo , number: newNumber}
        entryService
        .update(updatedPerson.id, updatedPerson)
        .then( returnedPerson => {
          setPersons(persons.map(person => Number(person.id) !== Number(updatedPerson.id) ? person : returnedPerson));
          setNewName('');
          setNewNumber('');
        })
    }
  }


  const submitInfo = (event) => {
    event.preventDefault();
    checkNameExists(newName) ? updatePersonInfo(newName, newNumber):
    addInfo(newName, newNumber);
  }

  const submitSearch = (event) => {
    event.preventDefault();
    const results = searchTermList(searchTerm, persons);
    console.log(results);
    const copyResults = [...results]
    setSearchList(copyResults);
    setSearchTerm('');

  }

  const handleNameChange = (event) => {
    setNewName(event.target.value)
  }

  const handleNumberChange = (event) => {
    setNewNumber(event.target.value)
  }


   const handleSearchTermChange = (event) => {
     setSearchTerm(event.target.value)
   }

   const deletePerson = (event) => {
     const personID = event.target.id
     const personInfo = persons.find( person => person.id === Number(personID))

     if (window.confirm(`Delete ${personInfo.name}?`)) {
       entryService
       .remove(personID).catch(error => {
       setMessage(
         `The information about '${personInfo.name}' has already been deleted from server`
      );
        setMessageStyle('error')
   })
 }
 }

  return (
    <>
      <Notification message = {message} messageStyle = {messageStyle}/>
      <h2>Phonebook</h2>
       <Filter handleSearchTermChange = {handleSearchTermChange} submitSearch = {submitSearch}  searchTerm = {searchTerm} />
      <h3>Add a New</h3>
      <PersonForm submitInfo = {submitInfo} handleNameChange = {handleNameChange} handleNumberChange = {handleNumberChange}
      newName = {newName} newNumber = {newNumber}/>
      <h2>Numbers</h2>
      <ul>
        {searchList.map((person) => <Name person = {person} deletePerson = {deletePerson}/>)}
      </ul>
  </>
  )
}

export default App
