import React from 'react'

const Filter = ({searchTerm, submitSearch, handleSearchTermChange}) => {
  return (
    <form onSubmit = {submitSearch}>
      <div>
        filter shown with: <input value = {searchTerm} onInput = {handleSearchTermChange}/>
      </div>
      <div>
        <button type="submit">Search</button>
      </div>
    </form>
  )

}

export default Filter
